import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import IngredientsList from '../components/IngredientsList'
import IngredientForm from '../components/IngredientForm'
import Loading from '../components/Loading'

import { getIngredients, addIngredient } from '../redux/actions/ingredients'

const Ingredients = ({
	ingredients,
	getIngredients,
	addIngredient,
	loading
}) => {
	useEffect(() => {
		getIngredients()
	}, [])

	const onSubmit = (payload) => {
		addIngredient(payload)
	}
	return loading ? (
		<Loading />
	) : (
		<div>
			<h2>Ingredients</h2>
			<IngredientForm onSubmit={onSubmit} />
			<IngredientsList ingredients={ingredients} />
		</div>
	)
}

const mapStateToProps = ({ ingredients: { ingredients, loading } }) => ({
	ingredients,
	loading
})

export default connect(mapStateToProps, { getIngredients, addIngredient })(
	Ingredients
)
