import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { useParams } from 'react-router-dom'

import { getIngredient, updateIngredient } from '../redux/actions/ingredients'
import IngredientItem from '../components/IngredientItem'
import IngredientForm from '../components/IngredientForm'
import Loading from '../components/Loading'

const Ingredient = ({
	ingredient,
	getIngredient,
	updateIngredient,
	loading
}) => {
	const { id } = useParams()

	useEffect(() => {
		getIngredient(id)
	}, [])

	const onSubmit = (payload) => {
		updateIngredient({ _id: id, ...payload })
	}

	return loading ? (
		<Loading />
	) : (
		<div>
			<IngredientItem {...ingredient} />
			<IngredientForm onSubmit={onSubmit} />
		</div>
	)
}

const mapStateToProps = ({ ingredients: { ingredient, loading } }) => ({
	ingredient,
	loading
})

export default connect(mapStateToProps, { getIngredient, updateIngredient })(
	Ingredient
)
